import React, { ReactNode } from 'react';
import { IconClose } from '../../basic/icon';
import { ModalHeader as ModalHeaderWrap, ModalHeaderTitle, ModalHeaderClose } from './styled';
import type { } from './Modal';


type IModalHeaderProps = {
  children: ReactNode;
};


export const ModalHeader = React.memo(
  React.forwardRef<HTMLDivElement, IModalHeaderProps>(
    (props, ref) => {
      return <ModalHeaderWrap ref={ref}>
        {props.children}
      </ModalHeaderWrap>;
    }
  )
);
